//header-hero styles
.container {
    padding: 0 32px;
}

@media screen and (max-width: 767px) {
    .container {
    padding: 0 20px;
    }
}

@media screen and (min-width: 1200px) {
    .container {
    padding: 0 16px;
    }
}



.hero-header{
    width: 100%;
    // height: 664px;
    background-color: #FFB8CA;
    z-index: -1;
    position: relative;
    overflow: hidden;
}
.hero-header__cont{
    position: relative;
    height: 664px;
    height: 502px;
    

    @media screen and (min-width: 768px) {
        .hero-header__cont {
        height: 416px;
        }
}

    @media screen and (min-width: 1200px) {
        .hero-header__cont {
        height: 664px;
        }
    }
}





// делаем заголовок, ссылки и цифры
.hero{
    padding-top: 150px;
}
.hero__left{
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}
.hero-pre {
    z-index: 1;
}

@media screen and (max-width: 767px) {
    .hero-pre {
    position: absolute;
    top: 194px;
    }
}

.hero__title{
font-family: var(--fontFamaly-primary);
font-weight: 400;
font-size: 26px;
line-height: 1.153;
letter-spacing: 0.04em;
text-transform: uppercase;
color: #ffffff;

}
@media screen and (max-width: 767px) {
    .hero__title {
    width: 218px;
    }
}

@media screen and (min-width: 768px) {
    .hero__title {
    font-size: 22px;
    line-height: 1.136;
    width: 162px;
    margin-bottom: 20px;
    }
}

@media screen and (min-width: 1200px) {
    .hero__title {
    width: 277px;
    margin-bottom: 24px;
    font-size: 38px;
    line-height: 1.157;
    }
}


.hero__title--big{
    font-family: var(--fontFamaly-primary);
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 0.958;
    text-transform: uppercase;
    color: var(--colorText-primary);
}

@media screen and (min-width: 768px) {
    .hero__title--big {
    font-size: 34px;
    line-height: 0.911;
    }
}

@media screen and (min-width: 1200px) {
    .hero__title--big {
    font-size: 58px;
    line-height: 0.896;
    }
}

.hero__list{
    display: flex;
}
.hero__item{
    display: flex;
}
.hero__item:last-child{
    margin-left: 8px;
}
@media screen and (min-width: 1200px) {
    .hero__item:last-child {
    margin-left: 12px;
    }
}

.hero__button{
font-family: var(--fontFamaly-secondary);
font-weight: 400;
font-style: normal;
border-radius: 20px;
font-size: 8px;
line-height: 1.25;
height: 24px;
transition: var(--default-transition);
}

@media screen and (min-width: 1200px) {
    .hero__button {
    font-size: 14px;
    line-height: 1.285;
    }
}

.hero__button--red{
    width: 62.5px;
    padding: 7px 13px;
    background-color: var(--colorText-primary);
    color: var(--colorText-secondary);
    &:hover, &:focus{
        background-color: #d41444bb;
    }
}
@media screen and (min-width: 1200px) {
    .hero__button--red {
    width: 107px;
    height: 40px;
    padding: 11px 23px;
    }
}

.hero__button--white{
width: 83px;
padding: 7px 13px;
background-color: var(--colorText-secondary);
color: var(--colorText-primary);

&:hover, &:focus{
    background-color: var(--bgc-btnHover);
}
}

@media screen and (min-width: 1200px) {
    .hero__button--white {
    width: 138px;
    height: 40px;
    padding: 11px 20px;
    }
}

@media screen and (max-width: 767px) {
    .hero__list--numbers {
    display: none;
    }
}

.hero__item--numbers{
    position: relative;
    
}
.hero__item--numbers:not(:last-child){
    margin-bottom: 16px;;
}

@media screen and (min-width: 1200px) {
    .hero__item--numbers:not(:last-child) {
    margin-bottom: 15px;
    }
}

.hero__item--numbers::after{
content: "";
position: absolute;
top: -1;
right: 0;
width: 16px;
height: 16px;
background-color: var(--colorText-secondary);
border-radius: 50%;
}

@media screen and (min-width: 1200px) {
    .hero__item--numbers::after {
    width: 30px;
    height: 30px;
    top: -4;
    right: 0;
    }
}

.hero__text--numbers{
font-family: var(--fontFamaly-primary);
font-weight: 400;
font-size: 28px;
line-height: 1.142;
letter-spacing: 0.04em;
text-transform: uppercase;
position: relative;
z-index: 1;
margin-bottom: 0;
width: 51px;
text-align: center;
color:var(--colorText-primary);
}

@media screen and (min-width: 1200px) {
    .hero__text--numbers {
    font-size: 46px;
    line-height: 1.152;
    position: relative;
    z-index: 1;
    width: 93px;
    margin-bottom: 2px;
    }
}


.hero__text--pre, .hero__text--sub{
font-family: var(--fontFamaly-secondary);
font-weight: 400;
font-size: 8px;
line-height: 1.25;
color: var(--colorText-secondary);
display: block;
}

@media screen and (min-width: 1200px) {
    .hero__text--pre, .hero__text--sub {
    font-size: 14px;
    line-height: 1.285;
    }
}

.hero__text--pre{
    display: block;
    text-align: center;
}

.hero-thumb__milk--img {
    width: 152px;
}

@media screen and (min-width: 1200px) {
    .hero-thumb__milk--img {
    width: 213px;
    }
}


// похиционируем картинку с молоком
.hero-thumb__milk{
    // position: absolute;
    // left: 15px;
    // bottom: 60px;
    display: none;
    
}

@media screen and (min-width: 768px) {
    .hero-thumb__milk {
    position: absolute;
    left: 32px;
    bottom: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    }
}

@media screen and (min-width: 1200px) {
    .hero-thumb__milk {
    position: absolute;
    left: 16px;
    bottom: 60px;
    }
}

.hero-thumb__decription{
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    
}
.hero-thumb__text{
font-family: var(--fontFamaly-secondary);
font-style: normal;
font-weight: 400;
font-size: 8px;
line-height: 1.5;
width: 114px;
height: 48px;
color: var(--colorText-secondary);

}


@media screen and (min-width: 1200px) {
    .hero-thumb__text {
    width: 200px;
    height: 78px;
    font-size: 14px;
    line-height: 1.42;
    }
}

.hero-thumb__btn{
    width: 18px;
    height: 18px;
    position: absolute;
    left: 0;
    bottom: 31px;
    background-color: transparent;
    border: none;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (min-width: 1200px) {
    .hero-thumb__btn {
    bottom: 33;
    }
}
.hero-thumb__btn {
    width: 18px;
    height: 18px;
    
}

@media screen and (min-width: 1200px) {
    .hero-thumb__btn {
    width: 26px;
    height: 26px;
    }
}
// позиционируем картинку с девушкой
.hero-thumb__girl{
    display: none;
}

@media screen and (min-width: 768px) {
    .hero-thumb__girl {
    display: block;
    position: absolute;
    right: 15px;
    bottom: 0;
    z-index: 2;
    width: 190px;
    }
}

@media screen and (min-width: 1200px) {
    .hero-thumb__girl {
    right: 32px;
    width: 304px;
    }
}

// делаем герой мороженки

.hero-thumb__ice{
    position: absolute;
    top: 83px;
    left: 36px;
    width: 346px;
    height: 344px;
    background: var(--colorText-third);
    border-radius: 50%;
}
@media screen and (min-width: 768px) {
    .hero-thumb__ice {
    top: 43px;
    left: 248px;
    width: 335px;
    height: 333px;
    }
}

@media screen and (min-width: 1200px) {
    .hero-thumb__ice {
    width: 534px;
    height: 534px;
    top: 54px;
    left: 516px;
    right: 189px;
    }
}

.hero__ice{
    position: absolute;
    top: 15px;
    left: 81px;
    width: 171px;
}

@media screen and (min-width: 768px) {
    .hero__ice {
    top: 37px;
    left: 74px;
    width: 143px;
    }
}

@media screen and (min-width: 1200px) {
    .hero__ice {
    top: 54px;
    left: 119px;
    width: 236px;
    }
}

.arrow-img{
    min-width: 18px;
    @media screen and (min-width: 1200px) {
        min-width: 26px;
}
}






