.section-testimonials {
    padding: 30px 0;
    @include media-min (768px) {
        padding: 30px 0 50px 0;
    }
    @include media-min (1200px) {
        padding: 60px 0;
    }
}

.slider__name {
    position: relative;
    padding-top: 36px;
    margin-bottom: 4px;

    font-size: 20px;
    line-height: 1.85;
    font-weight: 500;

    @include media-min (768px) {
        margin-bottom: 20px;
    }
}

.slider__name::before {
    content: '';
    position: absolute;
    background-image: url(../images/icons/red-dots.svg);
    
    width: 32px;
    height: 4px;
    top: 16px;
    left: 50%;
    transform: translateX(-50%);
}

.slider__thumb {
    width: 85px;
    margin-left: auto;
    margin-right: auto;
}

.slider__textfield {
    background-color: #fafafa;
    margin-top: 20px;
    
    padding: 43px 50px 17px;

    @include media-min (768px) {
        padding: 36px 6px;
    }

    @include media-min (1200px) {
        padding: 32px 74px;
    }

}

.slider__text {

    position: relative;
    font-size: 12px;
    line-height: 2.58;
    font-weight: 500;
    letter-spacing: 0.04em;

    padding-left: 10px;
    padding-right: 10px;

    background-image: repeating-linear-gradient(to bottom,
        transparent,
        transparent 29px,
        var(--border-decorative) 1px,
        var(--border-decorative) 30px);

        @include media-min (768px) {
            font-size: 14px;
            line-height: 2.14;
            padding-left: 50px;
            padding-right: 50px;
        }

        @include media-min (1200px) {
            padding-left: 35px;
            padding-right: 35px;
        
            font-size: 16px;
            line-height: 1.88;
        }         
}






/* Слайдер */
.slick-slider {
    margin-left: auto;
    margin-right: auto;
    max-width: 968px;
}


/* Ограничивающая оболочка */
.slick-list {
    overflow: hidden;
}

/* Лента слайдов */
.slick-track {
    display: flex;
    align-items: flex-start;
}

/* Слайд */
.slick-slide {
    position: relative;
    text-align: center;
    max-width: 968px;
}

.slider .slick-slide::before {
    content: '';
    position: absolute;

    top: 50px;
    left: 50px;
    width: 54px;
    height: 40px;
    background-image: url(../images/icons/quotes.svg);

    @include media-min (768px) {
        top: 40px;
        left: 6px;
    }

    @include media-min (1200px) {
        
        left: 74px;
    }
}


/* Точки (булиты) */
.slick-dots {
    display: flex;
    justify-content: center;
}

.slick-dots li {
    list-style: none;
    
}

.slick-dots li:not(:last-child) {
margin-right: 10px;
}

/* Активная точка */


/* Элемент точки */
.slick-dots li button {
    font-size: 0;
    width: 12px;
    height: 12px;
    background-color: #D9D9D9;
    border-radius: 50%;
    outline-style: none;
    border: none;
}

.slick-dots li.slick-active button {
    background-image: url(../images/icons/house.svg);
    // fill: #D31342;
    border-radius: 0%;
    background-color: transparent;
}