    max-width: 1200px;
    padding: 0 16px;
  }
}

.button {
  text-align: center;
  height: 44px;
  font-family: var(--fontFamaly-secondary);
  cursor: pointer;
  transition: background-color var(--default-transition), color var(--default-transition), transform var(--default-transition);
  border: none;
  border-radius: 22px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.button-arrow:after {
  content: "";
  width: 6px;
  height: 10px;
  background-image: url("Vector1.8cc95434.svg");
  margin-left: 12px;
  display: block;
}

.logo {
  width: 200px;
  height: 50px;
  transition: transform var(--default-transition);
  display: block;
  transform: scale(1);
}

.logo:hover, .logo:focus {
  transform: scale(1.1);
}

